@use "../abstracts/fonts" as f;
@use "../abstracts/" as a;

html {
  font: {
    // family: $ff-sans;
    // weight: $fw-light;
  }
}

body {
  font-size: f.$ff-size;
  line-height: 1.7;
}

h1,
h2,
h3 {
  line-height: 1;
  margin-bottom: a.$margin-b;
  font: {
    // family: $ff-titles;
  }
}

h1 {
  font: {
    size: 10vw;
  }

  @include a.mq(tablet) {
    font: {
      size: 5rem;
    }
  }

}

h2 {
  font: {
    size: 4rem;
  }

}

h3 {
  line-height: 1;
  margin-bottom: a.$margin-b;
  font: {
    size: 2.2em;
  }

  @include a.mq(desktop) {
    font-size: 3vw;
  }
}

strong {
  font: {
    weight: bold;
  }
}

.heading-one {
  // color: map-get($colors, white);
}

.heading-two {
  // color: map-get($colors, accent);
  text-transform: uppercase;;

  &--light {
    // color: map-get($colors, bcg-clr);
  }
}

.heading-three {
  // color: map-get($colors, bcg-clr);
}

.text-small {
  font: {
    size: 1rem;
  }
}
